.help-section {
  background: var(--bs-primary3);
  min-height: 100vh;
  padding-top: 40px;
  padding-bottom: 40px;
}

.help-box {
  border-radius: 12px;
  border: 1px solid rgba(0,0,0,0.08);
}

.help-image {
  max-width: 420px;
}

.help-title {
  color: var(--bs-primary2);
  font-weight: 700;
}

.form-control {
  border-radius: 10px;
  height: 48px;
  background: #ffffff;
  border: 1px solid rgba(0,0,0,0.1);
}

.form-control:focus {
  border-color: var(--bs-primary2);
  box-shadow: none;
}

textarea.form-control {
  height: auto;
  border-radius: 12px;
}

.help-btn {
  background-color: var(--bs-primary3);
  border: 2px solid var(--bs-primary2);
  padding: 14px;
  font-weight: 600;
  border-radius: 10px;
  color: var(--bs-primary2);
}


.help-btn:hover {
  background-color: var(--bs-primary1);
  color: var(--bs-primary2);
}

@media (max-width: 768px) {
  .help-image {
    width: 85%;
  }
}
